import React, { useContext, useEffect, useFuel } from 'react'
import { BrowserContext } from '../../store/BrowserContext';
import { MultiSelect } from 'react-multi-select-component';
import { TranslationContext } from '../../store/TranslationContext';

export const FuelsMultipleInputs = ({fuels ,setFuels}) => {
    const {translate} = useContext(TranslationContext)
    const customStrings = {
        allItemsAreSelected:translate("All fuels are selected"),
        selectAll:translate("Select all"),
        selectSomeItems:" ",
        create:translate("Create")
    };
    const {browserData} = useContext(BrowserContext)
    return (
    <div className='p-2'>
        <div>
            <label className="form-label"><strong>{translate('Fuel')}</strong></label>
            <MultiSelect className='hide-search' overrideStrings={customStrings} options={browserData.fuels.map(({id, name})=>({label : name, value : id}))} labelledBy="Select" value={fuels} onChange={setFuels}/>
        </div>
    </div>
    )
}