import React, { useContext, useState } from 'react'
import { BrowserContext } from '../../store/BrowserContext';
import { MultiSelect } from 'react-multi-select-component';
import { TranslationContext } from '../../store/TranslationContext';

export const DocumentsMultipleInputs = ({documents ,setDocuments}) => {
    const {translate} = useContext(TranslationContext)
    const customStrings = {
            allItemsAreSelected:translate("All documents are selected"),
            selectAll:translate("Select all"),
            selectSomeItems:" ",
            create:translate("Create")
      };
    
    const {browserData} = useContext(BrowserContext)
    return (
    <div className='p-2'>
        <div>
            <label className="form-label"><strong>{translate('Documents')}</strong></label>
            <MultiSelect className='hide-search' overrideStrings={customStrings} options={browserData.documents.map(({id, name})=>({label : name, value : id}))} labelledBy="Select" value={documents} onChange={setDocuments}/>
        </div>
    </div>
    )
}