import React, { useContext, useEffect, useRef } from 'react'
import styled from 'styled-components'
import { BrowserContext } from '../../store/BrowserContext'
import FiltrationParameters from './FiltrationParameters'
import ReactDOM from 'react-dom'

const StyliedComponent = styled.div`
width: 500px;
max-width: 100%;
background-color: var(--dark-white);
height: 100vh;
position: absolute;
top: 0;
transition: 0.5s;
overflow-y: auto;
overflow-x: hidden;
`

const FiltrationSidebar = () => {
    const {togglePopup, popupVisibility} = useContext(BrowserContext)
    const {language} =useContext(BrowserContext).browserData
   
    const sidebarStyle = {
        transform : `translateX(${popupVisibility.filtrationSidebarShown ? '0' : language == 'ar' ? '100%' : '-100%'})`,
    }
    const targetRef = useRef()
    const sideBarOpen = useRef()
    sideBarOpen.current = popupVisibility.filtrationSidebarShown 
    useEffect(() => {
        const handleGlobalClick = (event) => {
            const target = event.detail.target
            if (sideBarOpen.current && targetRef.current && !targetRef.current.contains(target)  ) {
                togglePopup()
            }
        };
        document.addEventListener('global-click', handleGlobalClick);
        return () => {
          document.removeEventListener('global-click', handleGlobalClick);
        };
    }, [])
  return (
    ReactDOM.createPortal(
        <StyliedComponent ref={targetRef} style={sidebarStyle}>
            <div className='bg-color-dark-white py-2 d-flex flex-row-reverse sticky-top z-2'>
                <button onClick={()=>togglePopup(false)}><i className="fa-solid fa-xmark fa-2xl color-theme my-4 mx-3"></i></button>
            </div>
            <div className='mnh-100'>
                <FiltrationParameters />
            </div>
            <div className='d-flex flex-row sticky-bottom'>
        </div>  
        </StyliedComponent>
        ,document.getElementById('filtration-sidebar__contaner'))
  )
}

export default FiltrationSidebar