import React, { useContext } from 'react'
import FormattedNumberInput from '../../Components/FormattedNumberInput'
import { TranslationContext } from '../../store/TranslationContext'

const PriceFiltrations = ({minPrice, setMinPrice, maxPrice, setMaxPrice}) => {
  const inputStyle = {minWidth : 60}
  const {translate} = useContext(TranslationContext)
  return (
    <>
        <div className='d-flex align-items-center gap-1 px-2'>
            <strong>{translate('Price')}</strong>
                <FormattedNumberInput style={inputStyle} min={0} formattedNumber={minPrice} setFormattedNumber={setMinPrice} className='border outline-0 rounded p-1 flex-grow-1' placeholder={translate('Min')} />-
                <FormattedNumberInput style={inputStyle}  formattedNumber={maxPrice} setFormattedNumber={setMaxPrice} className='border outline-0 rounded p-1 flex-grow-1' placeholder={translate('Max')} />
            <strong>{translate('Mi')}</strong>
        </div>
    </>
    
  )
}

export default PriceFiltrations