import React, { useContext, useEffect, usegearbox } from 'react'
import { BrowserContext } from '../../store/BrowserContext';
import { MultiSelect } from 'react-multi-select-component';
import { TranslationContext } from '../../store/TranslationContext';

export const GearboxsMultipleInputs = ({gearboxs ,setgearboxs}) => {
    const {translate} = useContext(TranslationContext)
    const customStrings = {
        allItemsAreSelected:translate("All gearboxes are selected"),
        selectAll:translate("Select all"),
        selectSomeItems:" ",
        create:translate("Create")
      };
    
    const {browserData} = useContext(BrowserContext)
    return (
    <div className='p-2'>
        <div>
            <label className="form-label"><strong>{translate('Gearbox')}</strong></label>
            <MultiSelect className='hide-search' overrideStrings={customStrings} options={browserData.gear_boxs.map(({id, name})=>({label : name, value : id}))} labelledBy="Select" value={gearboxs} onChange={setgearboxs}/>
        </div>
    </div>
    )
}