import React, { useContext, useEffect, useCarOption } from 'react'
import { BrowserContext } from '../../store/BrowserContext';
import { MultiSelect } from 'react-multi-select-component';
import { TranslationContext } from '../../store/TranslationContext';

export const CarOptionsMultipleInputs = ({options ,setOptions}) => {
    const {translate} = useContext(TranslationContext)
    const customStrings = {
        allItemsAreSelected:translate("All options are selected"),
        clearSearch:translate("Clear Search"),
        clearSelected:translate("Clear Selected options"),
        noOptions:translate("No option"),
        search:translate("Search"),
        selectAll:translate("Select all"),
        selectAllFiltered:translate("Select all (filtered)"),
        selectSomeItems:" ",
        create:translate("Create")
      };
    
    const {browserData} = useContext(BrowserContext)
    return (
    <div className='p-2'>
        <div>
            <label className="form-label"><strong>{translate('Car options')}</strong></label>
            <MultiSelect className='hide-search' overrideStrings={customStrings} options={browserData.options.map(({id, name})=>({label : name, value : id}))} labelledBy="Select" value={options} onChange={setOptions}/>
        </div>
    </div>
    )
}