import React, { useContext, useEffect, useState } from 'react'
import { BrowserContext } from '../../store/BrowserContext';
import { MultiSelect } from 'react-multi-select-component';
import { TranslationContext } from '../../store/TranslationContext';

export const StatesMultipleInputs = ({states ,setStates}) => {
    const {translate} = useContext(TranslationContext)
    const customStrings = {
        allItemsAreSelected:translate("All states are selected"),
        clearSearch:translate("Clear Search"),
        clearSelected:translate("Clear Selected options"),
        noOptions:translate("No state"),
        search:translate("Search"),
        selectAll:translate("Select all"),
        selectAllFiltered:translate("Select all (filtered)"),
        selectSomeItems:" ",
        create:translate("Create")
      };
    
    const {browserData} = useContext(BrowserContext)
    return (
    <div className='p-2'>
        <div>
            <label className="form-label"><strong>{translate('States')}</strong></label>
            <MultiSelect  overrideStrings={customStrings} options={browserData.states.map(({code, name})=>({label : name, value : code}))} labelledBy="Select" value={states} onChange={setStates}/>
        </div>
    </div>
    )
}