import React, { useContext, useEffect, useRef, useState } from 'react'
import { StatesMultipleInputs } from './StatesMultipleInputs'
import { BrowserContext } from '../../store/BrowserContext'
import { ColorsMultipleInputs } from './ColorsMultipleInputs'
import { DocumentsMultipleInputs } from './DocumentsMultipleInputs'
import PriceFiltrations from './PriceFiltrations'
import FormattedNumberInput from '../../Components/FormattedNumberInput'
import { CarOptionsMultipleInputs } from './CarOptionsMultipleInputs'
import { FuelsMultipleInputs } from './FuelMultipleInputs'
import { GearboxsMultipleInputs } from './GearboxMultipleInputs'
import { SearchContext } from '../../store/SearchContext'
import { useLocation, useNavigate } from 'react-router-dom'
import { isMobileDevice } from '../../utils'
import CategerySelect from './CategerySelect'
import BrandSelect from './BrandSelect'
import { TranslationContext } from '../../store/TranslationContext'
import {Loader} from '../../Components/Loader'

const currentYear = new Date().getFullYear();
const FiltrationParameters = () => {
    const {browserData, searchText, transformedParameters, togglePopup} = useContext(BrowserContext)
    const {setLoadingSearchedArticles, loadArticles, loadingSearchedArticles, setArticles} = useContext(SearchContext)

    let url = new URL(window.location);
    const location = useLocation()
    const APISearchParameters={}
    

    if (searchText) APISearchParameters.searchText = searchText
    let linkExtention = `?searchcategory=vehicles&searchtext=${url.searchParams.get('searchtext')}&`
    
    const statesListFromUrl = JSON.parse(url.searchParams.get('states')) || []
    const defaultStates = transformedParameters.states.filter(state =>statesListFromUrl.includes(state.value))
    const [states ,setStates] = useState(defaultStates)
    if (statesListFromUrl.length > 0) APISearchParameters.states = statesListFromUrl
    if (0 < states.length && states.length <  transformedParameters.states.length) linkExtention += `states=${JSON.stringify(states.map(state => state.value))}&` 

    const brandFromUrl = url.searchParams.get('brand') || -1
    const [brand, setBrand] = useState(brandFromUrl)
    if (brandFromUrl != -1 ) APISearchParameters.brand = brandFromUrl
    if (brand != -1) linkExtention += `brand=${brand}&`

    const categoryFromUrl = url.searchParams.get('category') || -1
    const [category, setCategory] = useState(categoryFromUrl)
    if (categoryFromUrl != -1 ) APISearchParameters.category = categoryFromUrl
    if (category != -1) linkExtention += `category=${category}&`

    const colorsListFromUrl = JSON.parse(url.searchParams.get('colors')) || []
    const defaultColors = transformedParameters.colors.filter(color =>colorsListFromUrl.includes(color.value))
    const [colors, setColors] = useState(defaultColors)
    if (colorsListFromUrl.length > 0) APISearchParameters.colors = colorsListFromUrl
    if (0 < colors.length && colors.length <  transformedParameters.colors.length) linkExtention += `colors=${JSON.stringify(colors.map(color => color.value))}&` 

    const documentsListFromUrl = JSON.parse(url.searchParams.get('documents')) || []
    const defaultDocuments = transformedParameters.documents.filter(document =>documentsListFromUrl.includes(document.value))
    const [documents, setDocuments] = useState(defaultDocuments)
    if (documentsListFromUrl.length > 0) APISearchParameters.documents = documentsListFromUrl
    if (0 < documents.length && documents.length <  transformedParameters.documents.length) linkExtention += `documents=${JSON.stringify(documents.map(document => document.value))}&`

    const optionsListFromUrl = JSON.parse(url.searchParams.get('options')) || []
    const defaultOptions = transformedParameters.options.filter(option =>optionsListFromUrl.includes(option.value))
    const [options, setOptions] = useState(defaultOptions)
    if (optionsListFromUrl.length > 0) APISearchParameters.options = optionsListFromUrl
    if (0 < options.length && options.length ) linkExtention += `options=${JSON.stringify(options.map(option => option.value))}&`


    const gearboxListFromUrl = JSON.parse(url.searchParams.get('gearboxes')) || []
    const defaultGearboxs = transformedParameters.gearBoxs.filter(gear =>gearboxListFromUrl.includes(gear.value))
    const [gearBoxs, setGearboxs] = useState(defaultGearboxs)
    if (0 < gearBoxs.length && gearBoxs.length <  transformedParameters.gearBoxs.length) linkExtention += `gearboxes=${JSON.stringify(gearBoxs.map(gear => gear.value))}&`


    const fuelListFromUrl = JSON.parse(url.searchParams.get('fuels')) || []
    const defaultFuels = transformedParameters.fuels.filter(fuel =>fuelListFromUrl.includes(fuel.value))
    const [fuels, setFuels] = useState(defaultFuels)
    if (fuelListFromUrl.length > 0) APISearchParameters.fuels = fuelListFromUrl
    if (0 < fuels.length && fuels.length <  transformedParameters.fuels.length) linkExtention += `fuels=${JSON.stringify(fuels.map(fuel => fuel.value))}&`


    const defaultMinPrice = url.searchParams.get('minprice') || ''
    const [minPrice, setMinPrice] = useState(defaultMinPrice)
    if (defaultMinPrice != '') APISearchParameters.min_price = defaultMinPrice
    if (minPrice != '') linkExtention += `minprice=${minPrice}&`

    const defaultMaxPrice = url.searchParams.get('maxprice') || ''
    const [maxPrice, setMaxPrice] = useState(defaultMaxPrice)
    if (defaultMaxPrice != '') APISearchParameters.max_price = defaultMaxPrice
    if (maxPrice != '') linkExtention += `maxprice=${maxPrice}&`

    const defaultMileage = url.searchParams.get('mileage') || ''
    const [mileage, setMileage] = useState(defaultMileage)
    if (defaultMileage != '') APISearchParameters.mileage = defaultMileage
    if (mileage != '') linkExtention += `mileage=${mileage}&`

    const defaultMinYear = url.searchParams.get('minyear') || ''
    const [minYear, setMinYear] = useState(defaultMinYear)
    if (defaultMinYear != '') APISearchParameters.min_year = defaultMinYear
    if (minYear != '') linkExtention += `minyear=${minYear}&`

    const defaultMaxYear = url.searchParams.get('maxyear') || ''
    const [maxYear, setMaxYear] = useState(defaultMaxYear)
    if (defaultMaxYear != '') APISearchParameters.max_year = defaultMaxYear
    if (maxYear != '') linkExtention += `maxyear=${maxYear}&`

    const defaultAllOptionOnly = JSON.parse(url.searchParams.get('alloptiononly')) || false
    const [allOptionOnly, setAllOptionOnly] = useState(defaultAllOptionOnly)
    if (defaultAllOptionOnly) APISearchParameters.allOptionOnly = true
    if (allOptionOnly) linkExtention += `alloptiononly=true&`
    
    const defaultExchangeOnly = JSON.parse(url.searchParams.get('exchangeenly')) || false
    const [exchangeOnly, setExchangeOnly] = useState(defaultExchangeOnly)
    if (defaultExchangeOnly) APISearchParameters.exchangeOnly = true
    if (exchangeOnly) linkExtention += `exchangeenly=true&`

    const CarConditionNewFromUrl = JSON.parse(url.searchParams.get('carconditionnew'))
    const defaultCarConditionNew = CarConditionNewFromUrl === false ? false : true
    const [carConditionNew, setCarCondtionNew] = useState(defaultCarConditionNew)
    if (!defaultCarConditionNew) APISearchParameters.carConditionNew = false
    if (!carConditionNew) linkExtention += `carconditionnew=false&`

    const CarConditionUsedFromUrl = JSON.parse(url.searchParams.get('carconditionused'))
    const defaultCarConditionUsed = CarConditionUsedFromUrl === false ? false : true
    const [carConditionUsed, setCarCondtionUsed] = useState(defaultCarConditionUsed)
    if (!defaultCarConditionUsed) APISearchParameters.carConditionUsed = false
    if (!carConditionUsed) linkExtention += `carconditionused=false&`

    useContext(SearchContext).APISearchParameters.current = APISearchParameters
    const navigate = useNavigate()
    const filterClickHandler=()=>{
        navigate('/search/' + linkExtention)
    }
    const setDefault = ()=>{ 
        setStates([])
        setBrand(-1)
        setCategory(-1)
        setColors([])
        setDocuments([])
        setMinPrice('')
        setMaxPrice('')
        setMileage('')
        setMinYear('')
        setMaxYear('')
        setOptions([])
        setAllOptionOnly(false)
        setFuels([])
        setGearboxs([])
        setExchangeOnly(false)
        setCarCondtionNew(true)
        setCarCondtionUsed(true)
    }  

    useEffect(()=>{
        setLoadingSearchedArticles(true)
        setArticles([]) 
        loadArticles(APISearchParameters, [])
    }, [location])

    const {translate} = useContext(TranslationContext)
    return ( 
    <>
        <div className='p-2'>
            <StatesMultipleInputs states={states} setStates={setStates}/>
            <CategerySelect category={category} setCtegory={setCategory}/>
            <BrandSelect category={category} setBrand={setBrand} brand={brand}/>
            <ColorsMultipleInputs colors={colors} setColors={setColors} />
            <DocumentsMultipleInputs documents={documents} setDocuments={setDocuments} />
            <hr/>
            <PriceFiltrations minPrice={minPrice} setMinPrice={setMinPrice} maxPrice={maxPrice} setMaxPrice={setMaxPrice} />
            <hr/>
            <div className='p-2'>
            <strong>{translate('Mileage')}</strong>  
            <FormattedNumberInput formattedNumber={mileage} setFormattedNumber={setMileage} placeholder={translate('Mileage')} className='border outline-0 rounded p-1 mx-2' /> <strong>{translate('KM')}</strong>
            </div>
            <div className='d-flex align-items-center gap-2 py-3 px-2'>
                <strong>{translate('Year')}</strong>
                <input type='number' min={1900} max={maxYear} value={minYear} maxLength={4} onChange={(e)=>{setMinYear(e.target.value)}} className='border outline-0 rounded p-1 flex-grow-1' placeholder={translate('Min')} />-
                <input type='number' max={currentYear} min={minYear} value={maxYear} maxLength={4} onChange={(e)=>{setMaxYear(e.target.value)}} className='border outline-0 rounded p-1 flex-grow-1' placeholder={translate('Max')} />
            </div>
            <hr/>
            <div className='d-flex align-items-center gap-2 py-3 px-2'>
                <strong className='h5'>{translate('All options only ?')}</strong>
                <input type="checkbox" checked={allOptionOnly} onChange={(e)=>{setAllOptionOnly(e.target.checked); setOptions([])}}/>
            </div>
            { !allOptionOnly && <CarOptionsMultipleInputs options={options} setOptions={setOptions} />}
            <hr/>
            <FuelsMultipleInputs setFuels={setFuels} fuels={fuels} />
            <GearboxsMultipleInputs setgearboxs={setGearboxs} gearboxs={gearBoxs} />
            <hr/>
            <div className='d-flex align-items-center gap-2 py-3 px-2'>
                <strong className='h5'>{translate('Exchange only')}</strong>
                <input type="checkbox" checked={exchangeOnly} onChange={(e)=>{setExchangeOnly(e.target.checked)}}/>
            </div>
            <hr/>
            <div className='p-2'>
                <strong className='h5'>{translate('Car condition')}</strong>
                <div className='d-flex align-items-center gap-3 px-2 justify-content-center my-2'>
                    <strong style={{width: 80}} className='h5'>{translate('Used')}</strong>
                    <input type="checkbox" checked={carConditionUsed} onChange={(e)=>{setCarCondtionUsed(e.target.checked)}}/> 
                </div>
                <div className='d-flex align-items-center gap-3 px-2 justify-content-center my-2'>
                    <strong style={{width: 80}} className='h5'>{translate('New')}</strong>
                    <input type="checkbox" checked={carConditionNew} onChange={(e)=>{setCarCondtionNew(e.target.checked)}}/> 
                </div>
            </div>
        </div>
        <div className='d-flex flex-row sticky-bottom' style={isMobileDevice() & !window.isWebView ? {paddingBottom: 56} : {}}>
            <button className='col-6 py-2 text-center bg-color-white color-theme' onClick={setDefault}>{translate('Reset')}</button>
            <button className='col-6 bg-color-theme py-2 text-center text-white d-flex gap-2 justify-content-center' onClick={filterClickHandler}><span>{translate('Filter')}</span>{ loadingSearchedArticles && <Loader diam={23} />}</button>
        </div>            
    </>
  )
}

export default FiltrationParameters