import React, { useState } from 'react';

const FormattedNumberInput = ({formattedNumber, setFormattedNumber, ...props}) => {
  const handleInputChange = (event) => {
    const inputValue = event.target.value.replace(/\D/g, ''); // Remove non-numeric characters
    const formattedValue = formatNumber(inputValue);
    setFormattedNumber(formattedValue);
  };

  const formatNumber = (number) => {
    // Add a space every three digits from the right
    return number.replace(/\B(?=(\d{3})+(?!\d))/g, ' ');
  };

  return (
   
      <input
        {...props}
        type="text"
        value={formattedNumber}
        onChange={handleInputChange}
      />
  );
};

export default FormattedNumberInput;
