import React, { useContext, useEffect, useState } from 'react'
import FiltrationSidebar from './FiltrationSidebar'
import { BrowserContext } from '../../store/BrowserContext'
import { TranslationContext } from '../../store/TranslationContext'

const Filteration = () => {
    const {togglePopup} = useContext(BrowserContext)
    const {translate} = useContext(TranslationContext)
    return (
        <div className='bg-color-white d-flex align-items-center'>
            <div className='margin-inline-start-auto px-3 cursor-pointer color-theme py-2' onClick={()=>togglePopup('filtrationSidebarShown')}>
                <i className="fa-solid fa-filter fa-xl mx-2"></i>
                <span>{translate('Filter')}</span>
            </div>
            <FiltrationSidebar/>
        </div>
    )
}

export default Filteration