import React, { useContext, useState } from 'react'
import { BrowserContext } from '../../store/BrowserContext';
import { MultiSelect } from 'react-multi-select-component';
import { TranslationContext } from '../../store/TranslationContext';

export const ColorsMultipleInputs = ({colors ,setColors}) => {
    const {translate} = useContext(TranslationContext)
    const customStrings = {
        allItemsAreSelected:translate("All colors are selected"),
        clearSearch:translate("Clear Search"),
        clearSelected:translate("Clear Selected options"),
        noOptions:translate("No color"),
        search:translate("Search"),
        selectAll:translate("Select all"),
        selectAllFiltered:translate("Select all (filtered)"),
        selectSomeItems:" ",
        create:translate("Create")
      };
    
    const {browserData} = useContext(BrowserContext)
    return (
    <div className='p-2'>
        <div>
            <label className="form-label"><strong>{translate('Colors')}</strong></label>
            <MultiSelect overrideStrings={customStrings} options={browserData.colors.map(({id, name})=>({label : name, value : id}))} labelledBy="Select" value={colors} onChange={setColors}/>
        </div>
    </div>
    )
}